<template>
    <div class="board">
      <div v-for="card in cards" :key="card.name">
        <my-card :image="card.image" :name="card.name"></my-card>
      </div>
    </div>  
</template>

<script>
import Card from "../Card/Card.vue";
export default {
  components: {
    "my-card": Card
  },
  created() {
    let imgSrc =
      "data:image/jpeg;base64,";
    for (var i = 1; i < 25; i++) {
      this.cards.push({ image: imgSrc, name: "test " + i });
    }
  },
  data() {
    return {
      cards: []
    };
  }
};
</script>
<style scoped>
.board {
  position: absolute;
  top: 10%;
  right: 0;
  bottom: 30%;
  left: 0;
  width: 65%;
  border-radius: 5px;
  padding: 5px;
  margin: auto;
  box-shadow: 0px 0px 1em #666;
  background-color: #fff;
  display: grid;
  grid-template-columns: repeat(8, 1fr);
  grid-auto-rows: minmax(100px, auto);
  grid-column-gap: 5px;
}
</style>




